import './App.css';
import React, { useState, useCallback } from 'react';

import Intro from './components/Intro';
import About from './components/About';
import Skills from './components/Skills';
import Project from './components/Project';
import Contact from './components/Contact';
import SideBarPos from './components/SideBarPos';

function App(){

    const [ sideBarState_intro, setSideBarState_intro ] = useState('hsla(240,2%,76%,0.3)');
    const [ sideBarState_about, setSideBarState_about ] = useState('hsla(240,2%,76%,0.3)');
    const [ sideBarState_skills, setSideBarState_skills ] = useState('hsla(240,2%,76%,0.3)');
    const [ sideBarState_projects, setSideBarState_projects ] = useState('hsla(240,2%,76%,0.3)');
    const handle_intro_onView = useCallback(() => {setSideBarState_intro('#E89529')},[]);
    const handle_intro_onNotView = useCallback(() => {setSideBarState_intro('hsla(240,2%,76%,0.3)')},[]);
    const handle_about_onView = useCallback(() => {setSideBarState_about('#E89529')},[]);
    const handle_about_onNotView = useCallback(() => {setSideBarState_about('hsla(240,2%,76%,0.3)')},[]);
    const handle_skills_onView = useCallback(() => {setSideBarState_skills('#E89529')},[]);
    const handle_skills_onNotView = useCallback(() => {setSideBarState_skills('hsla(240,2%,76%,0.3)')},[]);
    const handle_projects_onView = useCallback(() => {setSideBarState_projects('#E89529')},[]);
    const handle_projects_onNotView = useCallback(() => {setSideBarState_projects('hsla(240,2%,76%,0.3)')},[]);

    return(
        <div className='appDiv'>
            <Intro callbackview={ handle_intro_onView } callbacknotview={ handle_intro_onNotView }/>
            <About callbackview={ handle_about_onView } callbacknotview={ handle_about_onNotView }/>
            <Skills callbackview={ handle_skills_onView } callbacknotview={ handle_skills_onNotView }/>
            <Project callbackview={ handle_projects_onView } callbacknotview={ handle_projects_onNotView }/>

            <SideBarPos introstate={sideBarState_intro} 
                        aboutstate={sideBarState_about}
                        skillstate={sideBarState_skills}
                        projectstate={sideBarState_projects}/>
        </div>
    );
}

export default App;
