import './SideBarPos.css';
import React, { useEffect } from 'react';

function SideBarPos({introstate,aboutstate,skillstate,projectstate}){

    useEffect(
        () => {

        },
        []
    )

    return(
        <div className='sidebarpos'>
            <svg width='100%' height='100%' viewBox='0 0 100 250'>
                <path stroke='hsl(170,100%,30%)' d='M 25 10 v 170' />


                <text x='50px' y='32px' fill={introstate} fontSize='10px' textAnchor='start'>
                    Intro
                </text>

                <text x='45px' y='72px' fill={aboutstate} fontSize='10px' textAnchor='start'>
                    About
                </text>

                <text x='50px' y='112px' fill={skillstate} fontSize='10px' textAnchor='start'>
                    Skills
                </text>

                <text x='38px' y='152px' fill={projectstate} fontSize='10px' textAnchor='start'>
                    Projects
                </text>

                <circle r='6px' cx='90px' cy='30px' fill={introstate}/>
                <circle r='3px' cx='90px' cy='50px' fill='hsla(240,2%,76%,0.3)'/>
                <circle r='6px' cx='90px' cy='70px' fill={aboutstate}/>
                <circle r='3px' cx='90px' cy='90px' fill='hsla(240,2%,76%,0.3)'/>
                <circle r='6px' cx='90px' cy='110px' fill={skillstate}/>
                <circle r='3px' cx='90px' cy='130px' fill='hsla(240,2%,76%,0.3)'/>
                <circle r='6px' cx='90px' cy='150px' fill={projectstate}/>
            </svg>
        </div>
    );
}

export default SideBarPos;
